*
{
    margin: 0;
    padding: 0;
}

html,
body
{
    overflow: hidden;
}

.webgl
{
    position: fixed;
    top: 0;
    left: 0;
    outline: none;
}
.contact{
    position: absolute;
    bottom: 2vh;
    right:2vh;
    z-index: 9999;
}
.help{
    position: absolute;
    top: 2vh;
    right: 2vh;
    z-index: 1000;
}